import React, { useState, useEffect } from "react"
import Layout from '../../containers/layout'
import Ad from '../../containers/Ad'
import FormationFinder from '../../containers/FormationFinder'
import ThreeVFinder from '../../containers/ThreeVFinder'
import { getQueryString, assign, parseStringDate } from '../../helpers'
import BoxButton from '../../components/BoxButton'
import DateTimePicker from '../../components/DateTimePicker'


const Home = () => {
    const qs = getQueryString();
    const modes = [{
        label: 'Formation',
        component: <FormationFinder />
    }, {
        label: '3 Victories',
        component: <ThreeVFinder isoDate={qs.dt || new Date().toISOString()} />
    }]
    
    const [mode, setMode] = useState(modes[parseInt(qs.mode)] ? qs.mode : 0)
    return <Layout>
        <BoxButton 
         activeIndex={mode}
        onClick={(e, i) => {
            assign(`?mode=${i}`)
        }}
        buttons={modes} />
        {
            modes[mode].component
        }
        <Ad
            client="ca-pub-6343289823091285"
            slot="5114483936"
        />
    </Layout>
}

export default Home
